.back{width: 2000px;height: 2000px;background:rgba(0, 0, 0, 0.5);position: absolute;left: 0;top: 0;}
#box{display: none;background: #fff}
.boxs{width:300px;height: 200px;border: 1px solid #000;position: absolute;left: 0;right: 0;bottom: 0;top: 0;margin: auto;padding: 20px 20px 60px;}
textarea{width: 100%;height: 100%;}
.close{background: #333;display: inline-block;border: 1px solid #000;border-radius: 15%;text-align: center;line-height: 20px;width: 40px;height: 20px;margin-left: 130px;color: #fff;cursor: pointer}
.check{background: #333;display: inline-block;border: 1px solid #000;border-radius: 15%;text-align: center;line-height: 20px;width: 80px;height: 20px;margin: 10px 20px;color: #fff;cursor: pointer}


